.Home-container {
    max-width: 25vw;
    min-height: 30vh;
    margin: 15rem auto;
    border-radius: 5px;
    padding: 1rem;
    box-shadow: 0 8px 16px #0000001a;
}

.Home-container img {
    width: 15rem;
    border-radius: 10px;
}

p {
    margin: 1rem;
    white-space: pre-wrap;
    word-wrap: break-word;
    font-size: 2.5rem;
    font-weight: 200;
}

p span {
    font-weight: 350;
    font-style: italic;
    font-size: 2.3rem;
}

@media (max-width: 1920px) {
    .Home-container {
        min-width: 35vw;
        margin: 15rem auto;
        border-radius: 5px;
        padding: 1rem;
        box-shadow: 0 8px 16px #0000001a;
    }
}
@media (max-width: 1100px) {
    .Home-container {
        min-width: 70vw;
        margin: 8rem auto;
        border-radius: 5px;
        padding: 1rem;
        box-shadow: 0 8px 16px #0000001a;
    }
}
@media (max-width: 768px) {
    .Home-container {
        min-width: 70vw;
        margin: 8rem auto;
        border-radius: 5px;
        padding: 1rem;
        box-shadow: 0 8px 16px #0000001a;
    }
}
@media (max-width: 480px) {
    .Home-container {
        min-width: 90vw;
        margin: 3rem auto;
        border-radius: 5px;
        padding: 1rem;
        box-shadow: 0 8px 16px #0000001a;
    }
}